.tab{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  width:100%;
  height:40px;
  line-height: 40px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.tab-items{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: center;
}
a{
  text-decoration: none;
  -webkit-tap-highlight-color:transparent;
  tap-highlight-color:transparent;
  display: block;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  font-size: 14px;
  color: rgb(77,85,93);
}
a:hover{
  color: rgb(240,20,20);
}
.clearfix{/*通用clearfix写法*/
  display: inline-block;
}
.clearfix:after{
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}




































































































/*使用图标字体文件时应先引用图标字体文件的样式*/
@font-face {
  font-family: 'icomoon';
  src:  url(data:application/vnd.ms-fontobject;base64,);
  src:  url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
    url(data:application/x-font-ttf;base64,) format('truetype'),
    url(data:application/font-woff;base64,) format('woff'),
    url(data:image/svg+xml;base64,#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon"], [class*=" icon"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
